<script>
import { useRouter, useRoute } from 'vue-router';

export default {
  name: 'NavBar',
  setup: () => {
    const router = useRouter();
    const route = useRoute();

    const goTo = (routeName) => {
      router.push({ name: routeName });
    };

    const currentRoute = (name) => {
      return route.name === name;
    };

    return {
      goTo,
      currentRoute,
    };
  },
};
</script>

