// 武將與武將碎片分配結果
export async function getMartialAuctionResult(
  start = moment().tz('Asia/Taipei'),
  end = moment().tz('Asia/Taipei'),
) {
  const url = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/${tabName}/?key=${apiKey}`;
  let json;
  try {
    json = await (await fetch(url)).json();
  } catch (error) {
    window.alert('目前無法取得軍團拍賣的武將分配結果');
    console.error(error);
    json = undefined;
    return json;
  }
  console.log(`${formatTime(start)} ~ ${formatTime(end)}`);
  return json;
}

function formatTime(moment) {
  if (moment) {
    return moment.format('YYYY年MM月DD日 HH點mm分ss秒');
  }
  return '';
}
