import { createRouter, createWebHistory } from 'vue-router';
import MartialView from '../views/MartialView.vue';

const routes = [
  {
    path: '/titleUpgrade',
    name: 'titleUpgrade',
    component: () => import('../views/TitleUpgradeView.vue'),
  },
  {
    path: '/satin',
    name: 'satin',
    component: () => import('../views/SatinView.vue'),
  },
  {
    path: '/martial',
    name: 'martial',
    component: MartialView,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
