<template>
  <div class="martial">
    <div>
      <p class="my-3">最終統計(純文字版)：</p>
      <p>{{ getSimpleCompileResult(compileResult) }}</p>
    </div>
    <div>
      <p class="my-3">最終統計：</p>
      <div class="row g-0">
        <div
          v-for="(names, martial) in compileResult"
          v-bind:key="martial"
          class="card col-6 col-sm-4 col-md-3"
        >
          <div class="card-body">
            <h6
              class="card-title"
              v-bind:class="getMartialCountryColor(martial)"
            >
              {{ martial }}:
            </h6>
            <p class="text-break">{{ names }}</p>
          </div>
        </div>
      </div>
    </div>
    <div>
      <p class="my-3">登記紀錄：</p>
      <div class="table-wrapper">
        <table class="table">
          <thead>
            <tr>
              <th
                v-for="(title, index) in getTitles()"
                v-bind:key="index"
                scope="col"
                class="d-none d-sm-table-cell text-nowrap"
              >
                {{ title }}
              </th>
              <th
                v-for="(title, index) in getAlternativeTitles()"
                v-bind:key="index"
                scope="col"
                class="d-table-cell d-sm-none text-nowrap"
              >
                {{ title }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(datum, index) in data" v-bind:key="index">
              <th scope="row">{{ datum[0] }}</th>
              <td>{{ datum[1] }}</td>
              <td v-bind:class="getMartialCountryColor(datum[2])">
                {{ getMartialValue(datum[2]) }}
              </td>
              <td v-bind:class="getMartialCountryColor(datum[3])">
                {{ getMartialValue(datum[3]) }}
              </td>
              <td v-bind:class="getMartialCountryColor(datum[4])">
                {{ getMartialValue(datum[4]) }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { getMartialAuctionResult } from '@/services/api';

export default {
  name: 'MartialView',
  setup: () => {
    let data = ref([]);
    let compileResult = ref({});

    const getResult = async () => {
      return await getMartialAuctionResult();
    };

    const getTitles = () => {
      return [
        '時間戳記',
        '你的遊戲名稱',
        '第1順位武將',
        '第2順位武將',
        '第3順位武將',
      ];
    };

    const getAlternativeTitles = () => {
      return ['時間', 'ID', '#1', '#2', '#3'];
    };

    const getMartialValue = (value) => {
      if (!value) {
        return 'X';
      }

      const validPattern = /^[魏蜀吳群][-]/i;

      if (validPattern.test(value)) {
        return value;
      } else {
        return 'X';
      }
    };

    const getMartialCountryColor = (value) => {
      if (!value) {
        return '';
      }

      if (value.startsWith('魏')) {
        return 'color-wei';
      } else if (value.startsWith('蜀')) {
        return 'color-shu';
      } else if (value.startsWith('吳')) {
        return 'color-wu';
      } else if (value.startsWith('群')) {
        return 'color-heros';
      } else if (value.startsWith('不選擇')) {
        return '';
      } else {
        return '';
      }
    };

    const getCompileResult = (results) => {
      const map = {};

      // 不排除重複
      // const list = results.map((r) => {
      //   return {
      //     time: r[0],
      //     id: r[1],
      //     martial1: getMartialValue(r[2]),
      //     martial2: getMartialValue(r[3]),
      //     martial3: getMartialValue(r[4]),
      //   };
      // });

      // 排除重複，以最新登記時間的紀錄為主
      const distinctMap = results.reduce((r1, r2) => {
        r1[r2[1]] = {
          time: r2[0],
          id: r2[1],
          martial1: getMartialValue(r2[2]),
          martial2: getMartialValue(r2[3]),
          martial3: getMartialValue(r2[4]),
        };
        return r1;
      }, {});

      const list = Object.values(distinctMap);

      // 統計第1順位武將
      for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (map[item['martial1']]) {
          map[item['martial1']] += ', ' + item.id;
        } else {
          map[item['martial1']] = item.id;
        }
      }

      // 統計第2順位武將
      for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (map[item['martial2']]) {
          map[item['martial2']] += ', ' + item.id;
        } else {
          map[item['martial2']] = item.id;
        }
      }

      // 統計第3順位武將
      for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (map[item['martial3']]) {
          map[item['martial3']] += ', ' + item.id;
        } else {
          map[item['martial3']] = item.id;
        }
      }

      // 刪除不選擇的紀錄或是不合法的武將名稱
      delete map['X'];
      return map;
    };

    const getSimpleCompileResult = (compileResult) => {
      let simpleCompileResult = '';
      for (let martial in compileResult) {
        const names = compileResult[martial];
        simpleCompileResult += martial;
        simpleCompileResult += '：';
        simpleCompileResult += names;
        simpleCompileResult += ';\n';
      }
      return simpleCompileResult;
    };

    onMounted(async () => {
      const result = await getResult();
      data.value = result.values;
      // google sheet 的第1筆紀錄是標題，需要排除
      data.value.shift();
      compileResult.value = getCompileResult(data.value);
    });

    return {
      data,
      compileResult,
      getTitles,
      getAlternativeTitles,
      getMartialValue,
      getMartialCountryColor,
      getSimpleCompileResult,
    };
  },
};
</script>

<style scoped lang="scss">
@import '@/assets/css/main.scss';

.martial {
  .table-wrapper {
    overflow-y: auto;
  }

  table {
    table-layout: fixed;

    .d-table-cell {
      width: 120px;
    }
  }

  @media (max-width: 576px) {
    table {
      font-size: 12px;
    }
  }

  @media (min-width: 576px) {
    table {
      font-size: 14px;
    }
  }
}
</style>
